import { getOpenID } from "@/api/mine.js"
let self;
export default {
	data() {
		return {
			info: {
				nickName: null,
				islogin: 0,
				isgetuserinfo: !1,
				loginnote: 0,
				getcodetimes: 0,
				gettokentimes: 0
			}
		};
	},
	methods: {

		GetUserInfo({ detail }) {
			console.log(detail)
			this.info = { ...JSON.parse(detail.rawData) }
			this.$store.commit("user/SET_USER_INFO", this.info)
		},
		/* 运动步数统计 */
		toSportGoal() {
			// uni.showToast({
			// 	icon: "none",
			// 	title: "该功能暂未开放"
			// })
			uni.navigateTo({
				url: "./step/index"
			})
		},
		/* 密码设置 */
		toChangePwd() {
			uni.showToast({
				icon: "none",
				title: "该功能暂未开放"
			})
		},
		/* 常见问题 */
		Tousetips() {
			uni.navigateTo({
				url: './useTip/index'
			});

		},
		/* 权限设置 */
		openset() {
			uni.openSetting({
				success(res) {
					console.log(res.authSetting)
				}
			});
		},
		/* 需要智能设备 */
		apploadnote() {
			uni.showToast({
				icon: "none",
				title: "该功能暂未开放"
			})
		},
		init() {

			wx.login({
				success(res) {
					if (res.code) {
						//发起网络请求
						getOpenID({ code: res.code }).then(res => {
							console.log(res)
							self.$store.commit("user/SET_SESSION_KEY", res.session_key)
						}).catch(res => {
							console.log(res)
						})
					} else {
						console.log('登录失败！' + res.errMsg)
					}
				}
			})
		}

	},
	onShow() {
		try {
			this.info = this.$store.getters.userInfo
		} catch (e) {
			console.log(e)
			//TODO handle the exception
		}
	},
	created() {
		// console.log("你好")
		self = this
		const sessionKey = self.$store.getters.getSessionKey;
		if (!sessionKey) {
			this.init()
			return
		}
		wx.checkSession({
			success() {
				console.log("未过期")
				//session_key 未过期，并且在本生命周期一直有效
			},
			fail() {
				self.init()
				// session_key 已经失效，需要重新执行登录流程
				//重新登录
			}
		})
	}
}
